import {Component, OnInit, Input, ViewChild} from '@angular/core';
import {HighlightService} from '../programmes/highlight.service';
import spd from './../../../../wahlprogramme/spd.html';
import cdu from './../../../../wahlprogramme/cdu.html';
import afd from './../../../../wahlprogramme/afd.html';
import fdp from './../../../../wahlprogramme/fdp.html';
import linke from './../../../../wahlprogramme/linke.html';
import gruene from './../../../../wahlprogramme/gruene.html';

@Component({
  selector: 'content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.css'],
  providers: [HighlightService]
})
export class ContentComponent implements OnInit {

  parties = ['CDU/CSU', 'SPD', 'Die Grünen', 'Die Linke', 'FDP', 'AFD'];
  @Input() labels = [null, null, null, null, null];
  @Input() activeParties = [null, null]; // [left, right]
  programmes;
  fullUrl;
  shareUrl;
  fixedHost = "wahlkompass-digitales.de"
  programme_left = "Bitte Partei wählen";
  programme_right = "Bitte Partei wählen";
  left_default = "yes";
  right_default = "yes";

  constructor(private highligtService: HighlightService) {
    this.programmes = [cdu, spd, gruene, linke, fdp, afd];
  }

  ngOnInit() {
    console.log(this.activeParties);
    if ( this.activeParties[0] ) {
      this.showPartyLeft(this.activeParties[0]);
    }
    if ( this.activeParties[1] ) {
      this.showPartyRight(this.activeParties[1]);
    }
  }

  loadProgramme(party, index) {
    let position = this.getPosition(index);
    let myclass = ".left";
    if (index == 1) {
      myclass = ".right";
    }
    let id = this.parties.indexOf(party);
    let programme = this.programmes[id];
    $('.column.' + position).html(programme);
    $('.column.' + position + ' span[labels]').attr('active-labels', 0);
    this.highligtService.highlightAllLabels(position, this.labels);
  }

  showPartyLeft(party){
    console.log(party);
    this.activeParties[0] = party;
    this.loadProgramme(party, 0);
    this.buildUrl();
    this.programme_left = party;
    this.left_default = "no";
  }

  showPartyRight(party){
    this.activeParties[1] = party;
    this.loadProgramme(party, 1);
    this.buildUrl();
    this.programme_right = party;
    this.right_default = "no";
  }

  getPosition(index) {
    let position
    if (index === 0) {
      position = 'left';
    } else {
      position = 'right';
    }
    return position;
  }

  buildUrl() {
    this.fullUrl = this.constructUrl(),
    this.shareUrl = this.constructUrl(true);

  }

  constructUrl(urlify=false, hardcodeDomain=true) {
    let host;
    let basis;
    if (hardcodeDomain) {
      host = this.fixedHost;
    } else {
      host = window.location.host;
    }
    if (urlify) {
      basis = location.protocol.slice(0,-1) + '%3A//' + host;
    } else {
      basis = location.protocol + '//' + host;
    }
    basis += '?labels=';
    let labels_join = this.labels.filter(x => (x !== null) && (x !== '')).map(x => this.highligtService.getUrlfiedLabel(x)).join(",");
    basis += labels_join;
    basis += '&parties=';
    let parties_join = this.activeParties.map(x => this.parties.indexOf(x)).join(",");
    let parties = this.activeParties.filter(x => this.parties.indexOf(x) > -1).map(x => this.parties.indexOf(x));
    basis += parties.join(",");
    return basis;
  }

}



