import {Injectable} from '@angular/core';
import categories from './../../../../wahlprogramme/categories.json';

@Injectable()
export class HighlightService {

  activeToplevel;
  sublevel;
  categories;
  labels = ['steuern', 'schule', 'europa', 'infrastruktur', 'mint-bildung'];
  jumpers = {
    left: [],
    right: []
  };
  currentPosElement = [0, 0, 0, 0, 0];

  constructor() {
    this.categories = categories;
  }

  highlight(labels, label, i, preselector = '') {
    $(preselector + 'span[labels*="' + this.getUrlfiedLabel(label) + '"]').each(
      function () {
        let currentNumOfLabels = $(this).attr('active-labels');
        $(this).attr('active-labels', (++currentNumOfLabels));
        $(this).addClass('label-' + i);
        if ($(this).hasClass('has-label')) {
          $(this).addClass('has-multiple-labels');
        } else {
          $(this).addClass('has-label');
        }
        $(this).children('.label-' + i).addClass('selected-label');
        $(this).children('.label-' + i).text(label);
      }
    );
    this.fade_in_content(label);
    this.hide_content();
    this.trigger_jumper(labels)
  }

  highlightAllLabels(position, labels) {
    let preselector = '.' + position + ' ';
    if (labels != null) {
      for (let i = 0; i < labels.length; i++) {
        if(labels[i] != null) {
          this.highlight(labels, labels[i], i, preselector);
        }
      }
    }
  }

  getUrlfiedLabel(key) {
    for (var cat in categories) {
      for (var sub in categories[cat]) {
        for (var ground in categories[cat][sub]) {
          if (ground === key) {
            return categories[cat][sub][ground];
          }
        }
      }
    }
    return 'undefined';
  }

  fade_in_content(label) {
    $(".wrapper-inner:has(.has-label)")
      .show()
      .prev()
      .removeClass("hidden-inner");
  }

  hide_content() {
    $(".wrapper-inner:not(:has(.has-label))")
      .hide()
      .prev()
      .addClass("hidden-inner");
  }

  /* ----- BEGIN JUMPER ----- */


  trigger_jumper(labels){
    this.trigger_single_jumper(labels, "left");
    this.trigger_single_jumper(labels, "right");
  }

  trigger_single_jumper(labels, column){
    this.labels = labels;
    let counterId = "#" + column + "Counter"
    $(counterId).empty();
    this.populate_label_position_arrays(column);
    this.write_all_labels_into_jumper(column, counterId);
    let currentPosElement = this.currentPosElement
    let jumpers = this.jumpers[column]
    $(counterId + ' .tag').click(function (){
      let label_id = $(this).attr('label-id');
      let currentPointer = currentPosElement[label_id];
      let request_id = jumpers[label_id]["locations"][currentPointer];
      let jRequest = $("#" + request_id)
      $(this).children('.labelname')
             .children('.current-label-position')
             .text((currentPointer+1)+'/');
      if (currentPointer < jumpers[label_id]["locations"].length - 1)
          currentPosElement[label_id]++;
      else
          currentPosElement[label_id] = 0;
     let jProgramme =  $('.' + column + ' .wrapper-programme');
     jRequest.parents(".wrapper-inner").show();
     jRequest.parents(".wrapper-inner").prev(":header").removeClass("hidden-inner");
     jProgramme.animate({
        scrollTop: 0
     }, 0).animate({
        scrollTop: jRequest.offset().top - jProgramme.offset().top
     }, 0);
    });
  }

  populate_label_position_arrays(column) {
    for (let i_label = 0; i_label < this.labels.length; i_label++){
      let url_label = this.getUrlfiedLabel(this.labels[i_label])
      let search_p = "." + column + " p:has(span[labels*=" + url_label + "])"
      let search_li = "." + column + " li:has(span[labels*=" + url_label + "])"
      let all_p = $(search_p)
      let all_li = $(search_li)
      this.jumpers[column][i_label] = {
        name: this.labels[i_label],
        url_label: url_label,
        i: i_label,
        locations: [],
        all_p: all_p,
        all_li: all_li,
        position: 0
      }
      let temp_locations = []
      for (var i_p = 0; i_p < all_p.length; i_p++){
        temp_locations[i_p] = all_p[i_p].id
      }
      for (let i_li = 0; i_p < all_li.length + all_p.length; i_p++){
        temp_locations[i_p] = all_li[i_li].id
        i_li++;
      }
      this.jumpers[column][i_label]["locations"] = temp_locations.sort()
    }
  }
  
  write_all_labels_into_jumper(column, counterId) {
    for (let i = 0; i < this.jumpers[column].length; i++){
      let label = this.jumpers[column][i]
      if (label["name"] != null) {
        $(counterId).append(
          '<span class="tag label-' + (i) + '" label="' + label["name"] +
          '" label-id="' + i +
          '"><span class="labelname" style="display: none;">' +
          label["name"] +
          ': <span class="current-label-position"></span></span>' +
          label["locations"].length + '</span>'
        );
      }
    }
    $(counterId + ' .tag').hover(
      function() {
        $( this ).find('.labelname').show();
      }, function() {
        $( this ).find('.labelname').hide();
      }
    );
  }

}



