import {Component, ViewChild} from '@angular/core';
import {ContentComponent} from "./content/content.component";
import categories from './../../../wahlprogramme/categories.json';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  labels = [null, null, null, null, null];
  activeParties = [null, null];
  shareUrl;
  fullUrl;

  newLabels(labels) {
    this.labels = labels;
    this.content.labels = labels;
    this.content.buildUrl();
  }

  constructor() {
    // Read Url and set up filters
    this.configure();
  }

  @ViewChild(ContentComponent)
  private content: ContentComponent;

  getParameterByName(name) {
    let url = window.location.href;
    // name = name.replace(/[\[\]]/g, "\\$&");
    var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
      results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return results[2];
  }

  configure() {
    const parties = ['CDU/CSU', 'SPD', 'Die Grünen', 'Die Linke', 'FDP', 'AFD'];
    let labels = this.getParameterByName('labels');
    const activeParties = this.getParameterByName('parties');
    if (labels !== null && labels !== '') {
      let splitted = labels.split(',');
      splitted = splitted.map(x => this.getNiceLabel(x));
      for (let i = 0; i < splitted.length; i++){
        this.labels[i] = splitted[i];
      }
    }
    if (activeParties != null && activeParties !== '') {
      let splitted = activeParties.split(',');
      splitted = splitted.map(x => parties[Number(x)]);
      for (let i = 0; i < splitted.length; i++){
        this.activeParties[i] = splitted[i];
      }
    }
  }

  getNiceLabel(key) {
    for (var cat in categories) {
      for (var sub in categories[cat]) {
        for (var ground in categories[cat][sub]) {
          if (categories[cat][sub][ground] === key) {
            return ground;
          }
        }
      }
    }
    return 'undefined';
  }

  buildUrl() {
    this.content.buildUrl();
    this.fullUrl = this.content.fullUrl;
    this.shareUrl = this.content.shareUrl;
  }

}




