import {Component, OnInit, Output, Input, EventEmitter} from '@angular/core';
import categories from './../../../../wahlprogramme/categories.json';
import {HighlightService} from '../programmes/highlight.service';

import {ToArrayPipe} from './toArray.pipe';

@Component({
  selector: 'navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.css'],
  providers: [HighlightService]
})
export class NavigationComponent implements OnInit {
  activeToplevel;
  toplevel;
  groundlevel;
  sublevel = [];
  @Input() labels = [null, null, null, null, null];
  @Output() newLabels = new EventEmitter<any[]>();
  categories;
  private warning: Boolean = false;
  showAll = true;
  splittedCategories = [];
  labelsSelected = "no";
  constructor(private highlightService: HighlightService) {
    this.categories = categories;
  };

  ngOnInit() {

    this.toplevel = Object.keys(this.categories);
    this.activeToplevel = this.toplevel[0];
    this.sublevel = Object.keys(this.categories[this.activeToplevel]);
    this.groundlevel = this.categories[this.activeToplevel];

    this.getBreakpoints();

  }

  addNewLabel($event, sub) {

    this.toggleWarning();
    if (this.warning || ($event.target.classList.contains('active'))) {
      this.deleteLabel(sub);
    } else {
      let i = this.findIndex(this.labels)
      this.labels[i] = sub;
      this.add_label(sub, i);
      this.newLabels.emit(this.labels);
    }
    if (this.labels.filter(function(x){ return x == null }).length == 5) {
      console.log(this.labels);
      this.labelsSelected = "no";
    } else {
      this.labelsSelected = "yes";
    }

  }

  add_label(label, i) {
    this.highlightService.highlight(this.labels, label, i);
  }

  deleteLabel(cat) {
    const index = this.labels.indexOf(cat);
    this.labels[index] = null;
    this.newLabels.emit(this.labels);
    this.remove_label(index);
    this.highlightService.trigger_jumper(this.labels)
    this.toggleWarning();
  }

  updateCategories(category) {
    if (category !== 'alle') {
      this.showAll = false;
      this.activeToplevel = category;
      this.sublevel = Object.keys(this.categories[this.activeToplevel]);
      this.groundlevel = this.categories[this.activeToplevel];
    } else {
      this.activeToplevel = 'alle';
    }
  }

  getBreakpoints() {
    let allCategoriesList = [];

    for (const cat in categories) {
      for (const sub in categories[cat]) {
        for (const ground in categories[cat][sub]) {
          allCategoriesList.push(ground);
        }
      }
    }

    allCategoriesList = allCategoriesList.filter(this.onlyUnique);
    allCategoriesList = allCategoriesList.sort();

    const length = allCategoriesList.length;
    const chunk = Math.ceil(length / 4); // 4 Columns

    let i, j, tmp
    for (let i=0, j=allCategoriesList.length; i < j; i +=chunk) {
      tmp = allCategoriesList.slice(i, i+chunk);

      this.splittedCategories.push(tmp);
    }

  }

  onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }

  toggleWarning() {
    this.findIndex(this.labels) === -1 ? this.warning = true : this.warning = false;
  }

  findIndex(array) {
    for (let i = 0; i < array.length; i++) {
      if (array[i] == null) {
        return i;
      }
    }
    return -1;
  }

  remove_label(i) {
    console.log("Remove label at index: " + i)
    $('span.wrapper-sentence.label-' + (i)).each(function () {

      let currentNumOfLabels = $(this).attr('active-labels');
      if (currentNumOfLabels > 0) {
        $(this).attr('active-labels', (--currentNumOfLabels));
      }
      if ($(this).is('.has-multiple-labels')) {
        if ($(this).attr('active-labels') === '1') {
          $(this).removeClass('has-multiple-labels');
        }
      } else {
        $(this).removeClass('has-label');
      }
    })

    if (this.labels.filter(function(x){ return x == null }).length == 5) {
      console.log(this.labels);
      this.labelsSelected = "no";
    }

    // Remove little label
    $('.label-' + i).children('span.label-' + i).removeClass('selected-label');
    $('.label-' + i).children('span.label-' + i).text('');

    $('span[labels]').removeClass('label-' + (i));
    $('span[labels*="' + this.highlightService.getUrlfiedLabel(this.labels[i]) + '"]').parent().removeClass('has-label');

    this.highlightService.hide_content();
  }

}



